import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import {Router, Route, browserHistory} from 'react-router';
import Aluno from './component/Page/Aluno';
import Professor from './component/Page/Professor';
import Visitante from './component/Page/Visitante';
import Curso from './component/Page/Aluno/Curso';
import Mapa from './component/Page/Aluno/Mapa';
import Semestre from './component/Page/Aluno/Semestre';


ReactDOM.render(
    <Router history={browserHistory}>
        <Route path="/" component={App} />
        <Route path="/Aluno" component={Aluno} />
        <Route path="/Professor" component={Professor}/>
        <Route path="/Visitante" component={Visitante}/>
        <Route path="/Aluno/Curso" component={Curso} />
        <Route path="/Aluno/Semestre" component={Semestre} />
        <Route path="/Aluno/Mapa" component={Mapa} />
        <Route path="/Curso" component={Curso} />
    </Router>,
    document.getElementById('root')
);


