import React, { Component } from 'react';
import '../../../App';
import '../../../App.scss';
import $ from 'jquery';
import { serialize, deserialize } from "react-serialize";


class Select_periodo extends Component{

  constructor(props) {
    super(props);
    this.state = {
      value: 'coconut',
      titulo: 'Selecione o seu periodo',
      addClass: true
    };
    this.handleChange = this.handleChange.bind(this);
  }
  toggle() {
    this.setState({addClass: !this.state.addClass});
  }

  handleChange(event) {

    this.setState({value: event.target.value});
    const peri = $('form').find('select').val();
    const select_form = peri;
    console.log(select_form);
    const seri = serialize(select_form)
    event.preventDefault();
    console.log("serialize " + seri);

    $.ajax({
      method: "POST",
      url: 'http://my-json-server.typicode.com/ntc-dev/Json-teste/',
      dataType: 'html',
      type: 'json',
      data: 'periodo: ' + seri,
      success: function() {
        console.log("Deu certo");
      },
      statusCode: {
        404: function() {
          window.location.href = '/Aluno/Mapa';
        }}
    });
  }
  render() {

        return(
            <div>
                <form>
                    <select value={this.state.value} onChange={this.handleChange}>
                      <option value="DEFAULT">{this.state.titulo}</option>
                      <option value="1 Semestre">1 Semestre</option>
                      <option value="2 Semeste">2 Semeste</option>
                      <option value="3 Semestre">3 Semestre</option>
                    </select>
                </form>
            </div>
        );
    }
}

export default Select_periodo;
