import React, { Component } from 'react';
import withStyles from "@material-ui/core/styles/withStyles";
import Button from "../CustomButtons/Button.jsx";
import Card from "../Card/Card.jsx";
import CardBody from "../Card/CardBody.jsx";
import '../../App.scss';
import cardImagesStyles from "../../Assets/jss/material-dashboard-react/cardImagesStyles.jsx";
import axios from 'axios'


const API = 'http://my-json-server.typicode.com/ntc-dev/Json-teste/';
const DEFAULT_QUERY = 'posts';

class CardBasic extends React.Component {

  constructor(props) {
      super(props);
      this.state = {
        error: null,
        isLoaded: false,
        items: []
      };
    }

    componentDidMount() {
   this.setState({ isLoading: true });

   axios.get(API + DEFAULT_QUERY)
     .then(result => this.setState({
       hits: result.data.hits,
       isLoading: false
     }))
  
     .catch(error => this.setState({
       error,
       isLoading: false
     }));
 }

    componentDidMount() {
      fetch(API + DEFAULT_QUERY)
    .then(response => response.json())
      .then(json => console.log(json))
  }

  render() {
   const { error, isLoaded, items } = this.state;
   if (error) {
     return <div>Error: {error.message}</div>;
   } else if (!isLoaded) {
     return <div>Loading...</div>;
   } else {
     return (
       <ul>
         {items.map(item => (
           <li key={item.name}>
             {item.name} {item.price}
           </li>
         ))}
       </ul>
     );
   }
 }
}

export default withStyles(cardImagesStyles)(CardBasic);
