import React, { Component } from 'react';
import '../../../App';
import '../../../App.scss';
import logo from '../../../Assets/img/logo.png';
import logo_senac from '../../../Assets/img/logo_senac.jpg';

class Patrocinio extends Component{

    constructor(){
        super();
        this.state = {
            titulo: 'logo'
        };
    }

    render(){
        return(
            <div className="info_box">
                <div className="img_patrocinio">
                    <img src={logo} className="logo_patro" alt={this.state.titulo}/>
                </div>
                <div className="img_senac">
                    <img src={logo_senac} className="logo_senac" alt={this.state.titulo}/>
                </div>
            </div>
        )
    }
}


export default Patrocinio;
