import React, { Component } from 'react';
import Footer from '../Footer/Footer';
import Id_professor from '../../component/Page/Professor/Id_professor';
import Patrocinio from './Aluno/Patrocinio';

class Professor extends Component {

  constructor(){
    super();
    this.state = {
      titulo: 'Id professor'
    };
  }

  render() {
    return (
      <div className="container">
      <div className="box_container">
        <div className="box_Aluno">
          <div className="box">
            <div className="box_center">
              <h2 className="titulo_id">{this.state.titulo}</h2>
              <Id_professor/>
            </div>
          </div>
          <Patrocinio/>
        </div>
      </div>
      <Footer/>
    </div>
    )
  }
}

export default Professor;
