import React, { Component } from 'react';
import '../../../App';
import '../../../App.scss';
import $ from 'jquery';
import { serialize, deserialize } from "react-serialize";
import { Link } from 'react-router';


class Select_periodo extends Component{

  constructor(props) {
    super(props);
    this.state = {
      value: 'coconut',
      titulo: 'Selecione o seu periodo'

    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {

    this.setState({value: event.target.value});
    const peri = $('#periodo').find('select').val();
    const select_form = peri;
    console.log(select_form);
    const seri = serialize(select_form)
    event.preventDefault();
    console.log("serialize " + seri);

    $.ajax({
      method: "POST",
      url: '/Aluno/',
      dataType: 'html',
      type: 'json',
      data: 'periodo: ' + seri,
      success: function() {
        console.log("Deu certo");
      },
      statusCode: {
        404: function() {
          window.location.href = '/Aluno/Curso';
        }
      }

    });
  }
  componentDidMount() {
    var source="http://webservicemaps.azurewebsites.net/periodo.php";
    this.serverRequest = $.get(source, function (result) {
      console.log(result)
      if ( typeof(result) === 'string') {
        result = JSON.parse (result);
      }
      var arrTen = [];
      for (var k = 0; k < result.length; k++) {
          arrTen.push(<option key={result[k]} value={result[k]}> {result[k]} </option>);
      }
      this.setState({
        opts: arrTen
      });
    }.bind(this));
  }
  
    componentWillUnmount() {
    this.serverRequest.abort();
  }
  
      render(){
          return(
              <div>
                  <form onSubmit={this.handleSubmit}  id="periodo">
                  <select value={this.state.value} onChange={this.handleChange}>
                  <option value="DEFAULT">{this.state.titulo}</option>
                  {this.state.opts}
                </select>
  
                  </form>
              </div>
          );
      }
  }

export default Select_periodo;
