import React, { Component } from 'react';
import '../../../App';
import '../../../App.scss';
import logo from '../../../Assets/img/logo.png';
import logo_senac from '../../../Assets/img/logo_senac.jpg';

class Id_professor extends Component{

    constructor(){
        super();
        this.state = {
            placeholder: 'Id Professor',
            type: 'text'
        };
    }


    render(){
        return(
            <div>
                <div className="campoID">
                    <input type={this.state.type} name="id" placeholder={this.state.placeholder}/>
                </div>
            </div>
        );
    }
}

export default Id_professor;