import React, { Component } from 'react';
import '../../../App';
import '../../../App.scss';
import $ from 'jquery';
import { serialize, deserialize } from "react-serialize";

let select_= $('form').find('select').val();

class Select_periodo extends Component{

  constructor(props) {
    super(props);
    this.state = {
      value: 'coconut',
      titulo: 'Selecione o seu periodo'

    };
    this.handleChange = this.handleChange.bind(this);
  }


  handleChange(event) {

    this.setState({value: event.target.value});
    const peri = {select_}
    const select_form = peri;
    console.log(select_form);
    const seri = serialize(select_form)
    event.preventDefault();
    console.log("serialize " + seri);
  
    $.ajax({
      method: "POST",
      url: 'localhost:3000/Aluno',
      dataType: 'html',
      type: 'json',
      data: 'periodo: ' + seri,
      success: function() {
        console.log("Deu certo");
      },
      statusCode: {
        200: function() {
          window.location.href = '/Aluno/Semestre';
        }
      }

    });

  }
  
componentDidMount() {
  var source="https://next.json-generator.com/api/json/get/EkLrLBiqU";
  this.serverRequest = $.get(source, function (result) {
    var arrTen = [];
    for (var k = 0; k < result.length; k++) {
        arrTen.push(<option key={result[k]} value={result[k]}> {result[k]} </option>);
    }
    this.setState({
      opts: arrTen
    });
  }.bind(this));
}

  componentWillUnmount() {
  this.serverRequest.abort();
}

    render(){
        return(
            <div>
                <form onSubmit={this.handleSubmit}>
                <select value={this.state.value} onChange={this.handleChange} id="curso">
                {this.state.opts}
              </select>

                </form>
            </div>
        );
    }
}

export default Select_periodo;
