import React, { Component } from 'react';
import './App.scss';
import Btn_aluno from './component/botao/btn_aluno';
import Btn_professor from './component/botao/btn_professor';
import Btn_visitante from './component/botao/btn_visitante';
import './index.css';
import logo from './Assets/img/logo.png';



class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="container">
          <div className="header">
          	<header className="h-title">
              <div className="img_logo">
                <img src={logo} className="logo" alt="logo"/>
              </div>
              <div className="botoes">
                <Btn_aluno/><br/>
                <Btn_professor/><br/>
                <Btn_visitante />
              </div>
          	</header>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
