import React, { Component } from 'react';
import '../../App.scss';
import LodoNTC from '../../Assets/img/LodoNTC.png';

class footer extends Component{

    constructor(){
        super();
        this.state = {
            titulo: 'logo'
        };
    }

    render(){
        return(
            <div>
                <div className="footer">
                    <p>Desenvolvido por: <img src={LodoNTC} className="logo_lodoNTC" alt={this.state.titulo}/></p>
                </div>
            </div>
        );
    }
}

export default footer;